import React,{Fragment, useState} from 'react';
import Error from './Error';

const Ask = ({saveBudget, saveRestant, refreshAsk}) => {

    // Definir el state
    const [quantity,saveQuantity] = useState(0);
    const [error,saveError] = useState(false);

    // función para leer el presupuesto

    const defineBudget = e =>{
        saveQuantity(e.target.value,10);
    }

    // Definir presupuesto

    const addBudget = e =>{
        e.preventDefault();

        // validar
        if(quantity<1 || isNaN(quantity)){
            saveError(true);
            return;
        }
        saveError(false);
        saveBudget(quantity);
        saveRestant(quantity);
        refreshAsk(false);

    }

    return ( 
        <Fragment>
            <h2>Elegir el presupuesto</h2>
            {error ? <Error mensaje="El presupuesto es incorrecto" /> : null}
            <form
                onSubmit={addBudget}
            >
                <input 
                    type="number"
                    className="u-full-width"
                    placeholfer="Elegí el presupuesto"
                    onChange={defineBudget}
                />
                <input 
                    type="submit"
                    className="button-primary u-full-width"
                    value="Definir el presupuesto"
                />
            </form>
        </Fragment>
     );
}
 
export default Ask;