export const checkBudget = (budget, restant) => {
    let classRestant;

    if((budget / 4)>restant){
        classRestant='alert alert-danger';
    }else if((budget/2)>restant){
        classRestant='alert alert-warning';
    }else{
        classRestant='alert alert-success';
    }
    return classRestant;
}