import React,{Fragment} from 'react';
import {checkBudget} from '../helpers';

const ControlBudget = ({budget,restant}) => {
    return ( 
        <Fragment>
            <div className="alert alert-primary">
                Presupuesto: ${budget}
            </div>
            <div className={checkBudget(budget,restant)}>
                Restante: ${restant}
            </div>
        </Fragment>
     );
}
 
export default ControlBudget;