import React,{useState} from 'react';
import Error from './Error';
import shortid from 'shortid';

const Form = ({saveSpending, saveCreateSpending}) => {

    const [name, saveName] = useState('');
    const [quantity,saveQuantity] = useState(0);
    const [error, saveError] = useState(false);


    const addSpending = e =>{
        e.preventDefault();

        if(name==='' || quantity<1 || isNaN(quantity)){
            saveError(true);
            return;
        }

        saveError(false);
        const spend = {
            name,
            quantity,
            id: shortid.generate()
        }
        console.log(spend);
        saveSpending(spend);
        saveCreateSpending(true);
        saveName('');
        saveQuantity(0);

    }

    return ( 
        <form
            onSubmit={addSpending}
        >
            <h2>Agregá los gastos</h2>
            {error ? <Error mensaje="Hubo un error al agregar el gasto"/>:null}
            <div className="field">
                <label>Nombre del gasto</label>
                <input 
                    type="text"
                    className="u-full-width"
                    placeholder="Gasto"
                    value={name}
                    onChange={e => saveName(e.target.value)}
                />
            </div>
            <div className="field">
                <label>Cantidad</label>
                <input 
                    type="number"
                    className="u-full-width"
                    placeholder="Cantidad"
                    value={quantity}
                    onChange={e => saveQuantity(parseInt(e.target.value))}
                />

                <input 
                    type="submit"
                    className="button-primary u-full-width"
                    value="Agregar gasto"
                />
            </div>
        </form>
     );
}
 
export default Form;