import React,{Fragment, useState, useEffect} from 'react';
import Pregunta from './components/Ask';
import Form from './components/Form';
import List from './components/List';
import ControlBudget from './components/ControlBudget';

function App() {

  // State
  const [budget,saveBudget] = useState(0);
  const [restant, saveRestant] = useState(0);

  const [showask, refreshAsk] = useState(true);

  const [spendings, saveSpendings] = useState([]);

  const [spending, saveSpending] = useState({});
  const [createSpending, saveCreateSpending] = useState(false);

  useEffect(()=>{
    if(createSpending){
      saveSpendings([
        ...spendings,
        spending
      ])

      const budgetRestant = restant - spending.quantity;
      saveRestant(budgetRestant);
      saveCreateSpending(false);
    }
  },[spending,createSpending,spendings,restant])



  return (
    <Fragment>
      <div className="container">
        <header>
          <h1>Gasto Semanal</h1>
          <div className="contenido-principal contenido">
            {showask ? (
              <Pregunta 
                saveBudget={saveBudget}
                saveRestant = {saveRestant}
                refreshAsk = {refreshAsk}
              />
            ):(
              <div className="row">
                <div className="one-half column">
                  <Form 
                    saveSpending = {saveSpending}
                    saveCreateSpending = {saveCreateSpending}
                  />
                </div>
                <div className="one-half column">
                  <List 
                    spendings={spendings}
                  />
                  <ControlBudget 
                    budget = {budget}
                    restant = {restant}
                  />
                </div>
              </div>
            )}
          </div>

        </header>
      </div>
    </Fragment>
  )
    
}

export default App;
